<template>
  <div class="edit-page">
    <div class="edit-wrap">
      <div class="edit-title">
        <div class="title-text">创建活动-{{'当前活动类型'}}</div>
        <div class="title-switch">
          <span class="font-small">显示技术支持</span>
          <el-switch v-model="form.skilShow" active-color="#FC4A46" inactive-color="#E6E6E6"></el-switch>
        </div>
      </div>
      <div class="edit-con">
        <div class="preview">
          <div class="phone-top"></div>
          <div class="phone-main">
            <div class="phone-head">
              <div class="phone-show"></div>
              <div class="phone-title">
                {{form.activityName}}
              </div>
            </div>
            <div class="phone-content preview-wrap">
              <!-- 根据不同的活动类型，切换不同的预览模板 -->
              <component v-bind:is="curTemplate"></component>
            </div>
          </div>
          <div class="phone-footer"></div>
        </div>
        <div class="editor">
          <div class="editor-head">
            <div class="editor-head_lt">
              <el-button @click="editorShow(false)" :class="{'bt-active':!editorType}">基本设置</el-button>
              <span class="mar-10">|</span>
              <el-button @click="editorShow(true)" :class="{'bt-active':editorType}">更多设置</el-button>
            </div>
            <div class="editor-head_rt">带*的为必填项</div>
          </div>
          <hr style="margin-bottom: 30px">
          <div class="editor-main" v-show="!editorType">
            <el-form size="medium" label-width="100px" status-icon ref="ruleForm" :model="formData" :rules="rules">

              <el-form-item label="活动名称：" prop="name">
                <el-input v-model="form.activityName" style="width: 480px">
                  <i slot="suffix" class="v-icon">&#xe652;</i>
                </el-input>
              </el-form-item>

              <el-form-item label="活动时间：">
                <el-date-picker type="datetime" placeholder="选择日期" v-model="form.beginTime" style="width: 220px;"></el-date-picker>
                <span class="mar-10">至</span>
                <el-date-picker type="datetime" placeholder="选择日期" v-model="form.endTime" @change="dateCalculate" style="width: 220px;"></el-date-picker>
              </el-form-item>

              <el-form-item label="活动主图：">
                <el-radio-group v-model="form.activityImgShow">
                  <el-radio label="1">默认</el-radio>
                  <el-radio label="2">更换</el-radio>
                </el-radio-group>
                <div class="activity-img" v-if="form.activityImgShow=='2' ">
                  <div class="img-content">
                    <img :src="item" v-for="(item, index) in imgContent.arr" :key="'prechoise'+index">
                  </div>
                  <div style="display: flex;margin-top: 15px;">
                    <label class="label-button" for="imgFile">上传</label>
                    <span class="font-small mar-10">建议尺寸：750*500.jpg/.pmg/.gif,<br>不超过1M，最多上传5张轮显</span>
                    <input id="imgFile" type="file" style="visibility: hidden;width: 50px">
                  </div>

                  <div class="img-button">
                    <div class="btn btn-left" @click="imgToggle(0)"></div>
                    <div class="btn btn-right" @click="imgToggle(1)"></div>
                  </div>
                </div>
              </el-form-item>

              <el-form-item label="商品数量：">
                <el-radio-group v-model="limitShopCount">
                  <el-radio :label="false">不限</el-radio>
                  <el-radio :label="true" style="margin-left: 15px;">限制</el-radio>
                  <el-input v-model="form.goodsCount" class="number-limit_ipt" placeholder="份"></el-input>
                </el-radio-group>
              </el-form-item>

              <template>
                <el-form-item label="砍价金额：">
                  <el-input v-model="form.formalPrice" placeholder="原价：999" style="width: 100px"></el-input>
                  <span class="mar-10">砍至</span>
                  <el-input v-model="form.afterPrice" placeholder="低价：0" style="width: 100px"></el-input>
                </el-form-item>

                <el-form-item label="砍价设置：">
                  <span>报名邀请</span>
                  <el-input class="mar-15" v-model="activityHaggle.limitPerson" placeholder="0人" style="width: 60px"></el-input>
                  <span>帮忙可砍至低价。</span>
                  <span class="font-small">(参与者每次砍价数额将随机的产生)</span>
                </el-form-item>

                <el-form-item label="重复砍价：">
                  报名者每隔24小时可以再次给自己砍价
                </el-form-item>
              </template>

              <template>
                <el-form-item label="集卡设置：">
                  <span>参与者邀请</span>
                  <el-input class="mar-15" v-model="activityHaggle.limitPerson" placeholder="0人" style="width: 60px"></el-input>
                  <span>帮忙可集齐所有卡片。</span>
                  <span class="font-small">(设置人数越多，参与者集卡难度会增加，建议根据活动进行实际情况及时调整)</span>
                </el-form-item>

                <el-form-item label="卡片设置：">
                  <div class="cards-setting">
                    <div class="cards-setting_tip">
                      <p>点击图片可以更换，尺寸：390*520，1M以内， 最少5张，最多9张，不上传则默认显示。</p>
                      <el-button>
                        <i class="v-icon">&#xe660;</i>换一批</el-button>
                    </div>
                    <ul class="presetCards">
                      <li class="presetCard-item" v-for="(item, index) in presetCards" :key="'presetCard' + index">
                        <img :src="item.src" alt="">
                      </li>
                    </ul>
                    <el-form-item>
                      <div>
                        <span>每天可以有</span>
                        <el-input class="mar-15" v-model="activityHaggle.limitPerson" placeholder="0人" style="width: 60px"></el-input>
                        <span>人可以完成集卡</span>
                        <span>(此项不是必填项)</span>
                      </div>
                      <p class="font-small">(例如：商品总数为100份，设置每天20人完成集卡，商品最快5天时间 可以领完。)</p>
                    </el-form-item>
                  </div>
                </el-form-item>
              </template>

              <div class="dashed"></div>

              <el-form-item label="活动描述：">
                <quill-editor v-model="form.goodsDesciption" style="height: 190px" :content="content" ref="myQuillEditor" :options="editorOption" @change="onEditorChange($event)">
                </quill-editor>
              </el-form-item>

              <div class="dashed" style="margin-top: 86px"></div>

              <el-form-item label="活动分享：">
                <el-checkbox v-model="form.share"></el-checkbox>
                <span>勾选表示同意将活动公开分享到活动商城</span>
              </el-form-item>

              <el-form-item label="背景音乐：" v-show="false">
                <span style="margin-left: 10px;">默认背景音乐.mp3</span>
                <img style="padding: 5px;cursor: pointer;" @click="musicPlay" src="static/imgs/manage/editor/play.png">
                <el-switch v-model="form.isPlay" active-color="#FC4A46" inactive-color="#E6E6E6" style="float: left;margin-top:10px"></el-switch>
                <div class="music-box" v-show="form.isPlay">
                  <div>
                    <el-input v-model="musicName" :disabled="true" style="width: 220px;margin:20px 0;font-size: 12px;"></el-input>
                    <label class="label-button" for="musicFile">上传</label>
                    <p class="font-small">提示：请上传mp3格式的音乐，2M以内</p>
                    <input id="musicFile" type="file" style="visibility: hidden;" @change="musicUpload($event)">
                  </div>
                </div>
              </el-form-item>

              <el-form-item label="活动规则：">
                <el-input :rows="6" v-model="form.rule" type="textarea"></el-input>
              </el-form-item>

              <el-form-item label="兑奖信息：">
                <el-input :rows="5" type="textarea"></el-input>
              </el-form-item>

            </el-form>

          </div>
          <div class="dashed"></div>

          <div class="editor-more" v-show="editorType">
            <div class="editor-more-content">
              <div class="more-head">
                <ul>
                  <li>
                    <router-link tag="li" active-class="active" :to="{name:'collectEdit'}">客户信息收集</router-link>
                  </li>
                  <li>
                    <router-link tag="li" active-class="active" :to="{name:'sponsorEdit'}">主办方</router-link>
                  </li>
                  <li>
                    <router-link tag="li" active-class="active" :to="{name:'shareEdit'}">分享设置</router-link>
                  </li>
                </ul>
              </div>
              <router-view 
                :activityFormList="activityFormList"
                :industry="industry" 
                :shareSet="shareSet" 
                :activityShopList="activityShopList" 
                :previewType="previewType" 
                @sponsorChange="sponsorChange"></router-view>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="edit-footer">
      <div class="footer-main">
        <el-button>预览发布</el-button>
        <el-button @click="save">保存</el-button>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import Utils from 'src/lib/utils.js'
import Activity from 'src/server/activity.js'

import PreviewCardscollect from 'src/components/preview/cards-collect.vue'

// 图片资源
import defaultCardImg from 'src/components/preview/default-img/collectCards/theme1/card-ji.png'

export default {
  components: {
    PreviewCardscollect
  },
  data () {
    return {
      curTemplate: 'PreviewCardscollect', // 当前预览模板
      editorType: false,
      previewType: 1,
      limitShopCount: true, // 商品数量是否限制
      scrollT: 0,
      content: '',
      surplusDate: {},
      elGoodsDesciption: null, // 预览活动描述
      elindustryDesc: null, // 预览主办方信息描述
      activityNameList: {
        index: 0,
        arr: ['2', '3', '4']
      },
      editorOption: {
        // 富文本编辑器按键设置
        modules: {
          toolbar: [
            [
              'bold',
              'blockquote',
              'italic',
              'underline',
              'strike',
              'indent',
              'image'
            ]
          ]
        }
      },
      form: {
        id: null,
        activityName: '',
        activityImgShow: '1',
        mainImg: '', // 活动主图
        imgList: [1, 2, 3, 4, 5], // 主图列表
        skilShow: true,
        beginTime: '', // 活动时间
        endTime: '',
        limitPerson: '', // 邀请砍至低价的人数
        afterPrice: '', // 砍价低价
        formalPrice: '', // 砍价原价
        smsNotify: '', // 是否短信的通知
        goodsCount: '', // 商品数量
        goodsDesciption: '', // 商品描述
        share: '', // 是否分享
        musicUrl: '', // 音乐链接
        rule:
          '1.点按钮“我要参与参”加活动；\n2.每天都可以给自己或好友砍价；\n3.也可以邀请好友来给自己砍价；\n4.砍价到最低价后联系商家来兑换；',
        isPlay: '' // 是否有背景音乐
      },
      rules: {
        // 表单验证规则
        name: [
          {
            type: 'string',
            min: 0,
            max: 20,
            message: '最长20个汉字',
            trigger: 'change',
            required: true
          }
        ],
        phone: [
          {
            pattern: /^((13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8}$/,
            message: '请填写正确的手机格式',
            trigger: 'change'
          }
        ]
      },
      // 预设卡片
      presetCards: [
        {
          src: defaultCardImg
        },
        {
          src: defaultCardImg
        },
        {
          src: defaultCardImg
        },
        {
          src: defaultCardImg
        },
        {
          src: defaultCardImg
        }
      ],
      imgContent: {
        index: 0,
        arr: [1, 2, 3, 4]
      },
      musicFlag: false, // 音乐是否可播放
      musicName: '',
      music: new Audio(),

      // 客户信息收集
      activityFormList: [
        {
          id: 1,
          isNeed: true,
          name: '姓名',
          uaid: 100038
        },
        {
          id: 2,
          isNeed: true,
          name: '手机号',
          uaid: 100038
        }
      ],

      // 主办方信息
      industry: {
        industryLogo: '', // 机构标志
        industryName: '', // 机构名称
        industryPhone: '', // 机构电话
        industryAddress: '', // 机构地址
        industryProvince: '', // 机构省
        industryCity: '', // 机构市
        industryArea: '', // 机构区
        industryStreet: '', // 机构详细地址
        industryDesc: '', // 机构描述
        industryUrl: '' // 二维码
      },

      activityHaggle: {
        id: '',
        limitPerson: '',
        status: '',
        uaid: ''
      },

      // 门店信息列表
      activityShopList: [
        {
          activityId: '11', // 关联的活动id
          contacts: '22', // 门店联系人
          shopName: 'ddd', // 门店名
          id: '', //
          shopAddress: '33', // 门店地址
          shopPhone: '44' // 门店电话
        },
        {
          activityId: '',
          contacts: '',
          shopName: '',
          id: '',
          shopAddress: '',
          shopPhone: ''
        },
        {
          activityId: '',
          contacts: '',
          shopName: '',
          id: '',
          shopAddress: '',
          shopPhone: ''
        }
      ],

      // 分享设置
      shareSet: {
        shareDesc: '', // 分享描述
        shareTitle: '', // 分享标题
        isShowShareName: '', // 是否显示分享人昵称
        isShowShareTimes: '', // 是否显示分享次数
        thumbnailUrl: ''
      },
      params: null,
      flag: false
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log('all is true')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 基本设置和更多设置按钮切换
    editorShow (val) {
      this.editorType = val
      val && this.$router.push({name: 'collectEdit'})
    },
    // 监听活动描述信息改变事件
    onEditorChange ({ quill, html, text }) {
      this.content = html
      this.elGoodsDesciption.innerHTML = html
    },
    // 主图列表
    imgToggle (val) {
      console.log(this.imgContent.index)
    },
    // 音乐播放
    musicPlay (event) {
      let ele = event.target || event.srcElement
      if (this.musicFlag) {
        if (this.music.paused) {
          this.music.play()
          ele.src = 'static/imgs/manage/editor/pause.png'
        } else {
          this.music.pause()
          ele.src = 'static/imgs/manage/editor/play.png'
        }
      }
      console.log(this.music.src)
    },
    // 音乐上传
    musicUpload (event) {
      let ele = event.target || event.srcElement
      let file = ele.files[0]
      let url = URL.createObjectURL(file)
      this.music.src = url
      this.musicName = file.name
    },
    // 计算日期差
    dateCalculate () {
      let date = Utils.DateCalculate(this.form.beginTime, this.form.endTime)
      this.surplusDate = date
    },
    // 监听主办方信息描述改变事件
    sponsorChange (data) {
      this.elindustryDesc.innerHTML = data
    },
    forIn (obj1, obj2) {
      if (Array.isArray(obj2)) {
        obj1 = obj2
      } else {
        for (let item in obj2) {
          obj1[item] = obj2[item]
        }
      }
    },
    checkFlag (val) {},
    checkMessage () {
      this.flag = true
    },
    // 保存信息
    save () {
      this.checkMessage()

      Activity.save({
        data: this.params,
        methods: 'post',
        ContentType: 'application/json'
      }).then(res => {
        console.log(res)
      })
    },
    publish () {
      this.checkMessage()
      this.params.operation = 2

      Activity.save({
        data: this.params,
        methods: 'post',
        ContentType: 'application/json'
      }).then(res => {
        console.log(res)
      })
    }
  },
  mounted () {
    this.music.oncanplay = () => {
      this.musicFlag = true
      this.music.volume = 0.4
    }
    document.querySelector('html').classList.add('previewPage')
    this.elGoodsDesciption = document.getElementById('goodsDesciption')
    this.elindustryDesc = document.getElementById('industryDesc')
    let body = document.body

    // 手机预览层高度固定
    body.addEventListener(
      'scroll',
      () => {
        if (body.scrollTop >= 220) {
          this.scrollT = `top:${body.scrollTop - 220 + 90}px`
        } else {
          this.scrollT = 'top:90px'
        }
      },
      false
    )

    // 获取活动ID
    let id = this.$route.params.id
    console.log(id)
  },
  beforeDestroy () {
    document.querySelector('html').classList.remove('previewPage')
  }
}
</script>

<style lang="scss" scoped>
