  height: calc(100vh - 96px);
  .v-icon {
    color: #fc4a46;
  }
  .el-form-item__content {
    line-height: 1.5;
  }
  .edit-wrap {
    position: relative;
    height: 100%;
    margin: 35px 25px 0;
    padding-bottom: 95px;
    box-sizing: border-box;

    .edit-title {
      position: relative;
      height: 48px;
      padding: 0 25px;
      background: #f9f9f9;
      border: 1px solid #e5e5e5;
