              background: url(/static/imgs/manage/editor/phone-title2.png) no-repeat;
              background-size: 100%;
            }
          }

          .phone-content {
            height: 486px;
            overflow-x: hidden;
            overflow-y: auto;
          }
        }

        .phone-footer {
          width: 100%;
