            background-position: 98% 50%;
          }

          .actName {
            background-color: #eeeeee;
            background-image: url(/static/imgs/manage/editor/select.png);
            background-repeat: no-repeat;
            background-position: 98% 50%;
          }
          .activity-img {
            width: 480px;

            box-sizing: content;
            padding: 27px 25px;
            border: 1px solid #d9d9d9;
            position: relative;
            top: 10px;

            .img-content {
              display: flex;
              justify-content: space-between;
              img {
                width: 96px;
                height: 65px;
              }
            }
            .btn {
              position: absolute;
              width: 0;
              height: 0;
              border-top: 8px solid transparent;
              border-bottom: 8px solid transparent;
              border-radius: 2px;
              top: 54px;
              cursor: pointer;
            }
            .btn-left {
              left: 6px;
              border-right: 12px solid rgba(255, 72, 63, 1);
            }
            .btn-right {
              right: 6px;
              border-left: 12px solid rgba(255, 72, 63, 1);
            }
          }
          .number-limit_ipt {
            width: 120px;
            margin-left: 20px;
            text-align: right;
          }
          .cards-setting {
            border: 1px solid #fff;
