                margin-right: 10px;
                margin-bottom: 10px;

                img {
                  width: 100%;
                  height: 100%;
                }
              }
            }
          }

          .music-box {
          }
        }

        .editor-more {
          .editor-more-content {
            width: 584px;
            border: 1px solid #d9d9d9;
            margin: 30px 0;

            .more-head {
              height: 38px;
              background: #f2f3f3;

              ul li {
                float: left;
                line-height: 38px;
                text-align: center;
                width: 120px;
                font-size: 14px;
                color: #5a5a5a;
                border-right: 1px solid #d9d9d9;
                cursor: pointer;

                a {
                  color: #000;
