              .active {
                background: #fff;
                border-top: 2px solid #fc4a46;
              }
            }
          }
        }
      }
    }
  }
  .edit-footer {
    position: relative;
    display: flex;
    height: 75px;
    padding-left: 50%;
    align-items: center;
    margin-top: -75px;
    border-top: 1px solid #fff;
    background: #f6f8f9;
  }
}
</style>


<style type="text/css">
.el-radio__input.is-checked .el-radio__inner {
  border-color: #fc4a46;
  background: #fc4a46;
}
.el-radio__input.is-checked + .el-radio__label {
  color: #fc4a46;
}
</style>


